@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  font-family: "Poppins";
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  max-height: 100vh;
  max-width: 100vw;
}
h1 {
  margin: 10px;
}

a {
  text-decoration: none;
  color: black;
}
a:hover {
  text-decoration: underline;
}
ul {
  margin: 2vh 2vw 4vh;
  list-style: none;
  display: flex;
  gap: 5vw;
}

form {
  display: flex;
  flex-direction: column;
  gap: 5px;
  align-items: center;
}

.btn {
  max-width: 100%;
  min-width: 50%;
  border: 2px solid rgba(0, 0, 0, 0.13);
  border-radius: 10px;
  padding: 5px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.171);
  transition: 0.2s ease-in-out;
}

.btn:active {
  background-color: rgba(0, 0, 0, 0.342);
  max-width: 100%;
  min-width: 50%;
  border: 2px solid rgba(0, 0, 0, 0.342);
  border-radius: 10px;
  padding: 5px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.342);
}

.about {
  margin: 10px;
}

.input {
  min-width: 20vw;
  border: 2px solid rgba(0, 0, 0, 0.13);
  border-radius: 10px;
  padding: 5px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.171);
  transition: 0.2s ease-in-out;
}

.input-correct {
  outline: rgb(9, 255, 0);
  min-width: 20vw;
  border: 2px solid rgba(9, 255, 0);
  border-radius: 10px;
  padding: 5px;
  box-shadow: 0px 0px 10px 0px rgba(9, 255, 0);
}

.input-error {
  outline: red;
  min-width: 20vw;
  border: 2px solid rgba(255, 0, 0, 0.877);
  border-radius: 10px;
  padding: 5px;
  box-shadow: 0px 0px 10px 0px rgba(255, 0, 0, 0.897);
}

.Answer-container {
  display: flex;
  /* min-width: 100vw; */
  width: 100%;
  justify-content: center;
}

.main {
  min-height: 100vh;
  max-width: 100vw;
  display: flex;
}

.Cards-container {
  display: flex;
  max-width: 100%;
  min-height: 65vh;
  align-items: center;
  justify-content: center;
}
.Cards {
  display: grid;
  grid-template-rows: 90% 10%;
  align-items: center;
  justify-content: center;
  justify-items: center;
  min-height: 50vh;
  min-width: 30vw;
  max-width: 30vw;
  border: 1px solid rgba(0, 0, 0, 0.13);
  border-radius: 25px;
  box-shadow: 0px 0px 9px 5px rgba(0, 0, 0, 0.171);
}

.Cards select,
.Cards button {
  width: 30vh; /* largeur identique */
  /* hauteur identique */
  font-size: 1rem; /* ou la taille de texte souhait√©e */
  margin: 0 5px; /* exemple de marge horizontale */
  border: 2px solid rgba(0, 0, 0, 0.13);
  border-radius: 10px;
  background: #ffffff41;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.171);
  transition: 0.1s ease-in-out;
}

.Cards button:active {
  width: 30vh;
  background-color: rgba(0, 0, 0, 0.342);
  border: 2px solid rgba(0, 0, 0, 0.342);
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.342);
}

@media (max-width: 1050px) {
  .Cards-container {
    display: grid;
    max-width: 100vw;
    grid-template-columns: 100%;
    align-items: center;
    justify-content: center;
    justify-items: center;
  }
  .Cards {
    width: 100%;
    max-width: none;
  }
  .input {
    width: 50%;
  }
  .form-validate {
    width: 100%;
  }
}

.test-container {
  margin-top: 30px;
  display: grid;
  grid-template-columns: 100%;
  justify-items: center;
  gap: 30px;
}

.btn-test {
  max-width: 100%;
  min-width: 50%;
  border: 2px solid rgba(0, 0, 0, 0.13);
  border-radius: 10px;
  padding: 5px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.171);
  transition: 0.2s ease-in-out;
}

.btn-test:active {
  background-color: rgba(0, 0, 0, 0.342);
  max-width: 100%;
  min-width: 50%;
  border: 2px solid rgba(0, 0, 0, 0.342);
  border-radius: 10px;
  padding: 5px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.342);
}
